
.title_area[data-v-caa59672] {
    padding: 45px 0;
    background: #f7f7f1;
}
.brand-tip-1[data-v-caa59672] {
    text-align: center;
    font-size: 30px;
    background: url(../assets/images/icon_successful.png) no-repeat #f7f7f1;
    background-position: 25% center;
    background-size: 28px;
    color: #FF8A00;
}
.brand-tip-2[data-v-caa59672] {
    text-align: center;
    background: #f7f7f1;
    margin-top: 25px;
    font-size: 20px;
    color: #FF8A00;
}
/* 评论 */
.grade[data-v-caa59672] {
    padding-left: 15px;
    padding-right: 15px;
}
.fs15[data-v-caa59672] {
    font-size: 15px;
}
.star[data-v-caa59672] {
    width: 20px;
    height: 20px;
    float: left;
    margin: 6px 6px 2px 6px;
    background-size: 100%;
    background-repeat: no-repeat;
}
.star.on[data-v-caa59672]{
    background-image: url(../assets/images/icon_xingxing_xuanzhong_03.png) ;
}
.star.off[data-v-caa59672] {
    background-image: url(../assets/images/icon_xingxing_weixuanzhong_03.png);
}
.p15[data-v-caa59672] {
    padding: 15px;
}
.content[data-v-caa59672] {
    position: relative;
    height: 100px;
    margin: 0 15px;
    background-color: #fffff8;
    border: 1px solid #d4cfc8;
    outline: none;
}
.content[data-v-caa59672]:before {
    content: "亲，您的评价对师傅有很大的鼓励哦 ~";
    position: absolute;
    top: 50%;
    margin-top: -10px;
    left: 50%;
    margin-left: -120px;
}
.content[data-v-caa59672]:before {
    font-size: 13px;
    color: #bfbfbf;
}
.content[data-v-caa59672]:focus:before, 
.content.hasvalue[data-v-caa59672]:before {
	display: none;
}
/* 上传图片 */
.photo_area[data-v-caa59672] {
    font-size: 15px;
    color: #7e6b5a;
    padding-left: 10px;
}
.photo_wrap[data-v-caa59672] {
    width: 33%;
    text-align: center;
    display: inline-block;
    height: 100px;
    float:left;
}
.addicon[data-v-caa59672] {
    background: url('../assets/images/add_photo.png') no-repeat;
    background-size: 40px;
    background-position: 50%;
}
.photo[data-v-caa59672] {
    margin: 3px;
    width: 93%;
    height: 96px;
    /* outline: #e5e2dd solid 1px; */
}
.btn-fixed[data-v-caa59672] {
    position: fixed;
    bottom: -15px;
    left: -15px;
    right: -15px;
}
.btn[data-v-caa59672] {
    display: block;
    margin: 15px;
    height: 44px;
    line-height: 44px;
    color: #fff!important;
    font-size: 15px;
    text-align: center;
    background-color: #ff8a00;
    border-radius: 3px;
    outline: none;
    border: none;
    text-decoration: none;
}



