    -webkit-animation: bouncedelay-data-v-03cd369c 1.4s infinite ease-in-out;
    animation: bouncedelay-data-v-03cd369c 1.4s infinite ease-in-out;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}
.load6 .bounce1[data-v-03cd369c] {
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
}
.load6 .bounce2[data-v-03cd369c] {
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
}
@-webkit-keyframes bouncedelay-data-v-03cd369c{
0%,80%,100%{-webkit-transform:scale(0.0)
}
40%{-webkit-transform:scale(1.0)
}
}
@keyframes bouncedelay-data-v-03cd369c{
0%,80%,100%{transform:scale(0.0);-webkit-transform:scale(0.0)
}
40%{transform:scale(1.0);-webkit-transform:scale(1.0)
}
}
